
                @use "sass:color";
                @use "sass:math";
                @import "./sass/0-settings/_colors.scss";
              
@import "../../sass/base";

.m-text-media {
  @include grid();
  gap: $wa-spacing-07;
  @include tablet {
    gap: $wa-spacing-06
  }

  &__text,
  &__media {
    @include grid-item-span(
      $col-span-mobile: $grid-cols-mobile,
      $col-span-tablet: 3,
      $col-span-desktop: 6
    );
  }



  &__text {
    [data-component-id="wateraid:rich_text"] {
      margin: 0;
    }
  }

  .link--text {
    margin-top: $wa-spacing-07;
  }

  &__media {
    margin: 0;
  }

  &--image_left {
    .m-text-media__media {
      order: -1;
    }
  }
}
